import React, { FC, } from 'react';
import './App.css';

const App: React.FC = () => {
  const header = () => {
    return  (
      <div style={{ textAlign: 'center', fontSize: 28 }}>
        My Web App
        <div style={{ fontSize: 14 }}>
          version 1.0.0.2
        </div>
      </div>
    )
  };
const constraints = {
    audio: false,
    video: {
      facingMode: 'user'
  }
};
  const start = async () => {
    const stream = await navigator.mediaDevices.getUserMedia(constraints)
    const video = document.getElementById('video') as HTMLVideoElement;
    video.style.width = '100%';
    video.style.height = '100%';
    video.setAttribute("playsinline", "true");
    video.setAttribute("controls", "true");
    video.srcObject = stream;
    video.onloadedmetadata = function(e) {
      video.play();
    }
}
  
  return (
    <div>
      <header>
      {header()}
      </header>
      <div
      style={{
        margin: '0.5rem', minHeight: '100vh',
        }}>
        <div style={{
          fontSize: 18, fontFamily: 'Playfair Display',
          marginBottom: '0.2rem', marginTop: '2rem',
          fontWeight: 'bold' }}>
          Who am I?
        </div>
          I am a Front end developer for ST Electronics in Singapore.
        I am interested in web and mobile development with great passion for
        UI effects, animations and creating intuitive, dynamic user experiences.
      </div>
      <div style={{
          fontSize: 18, fontFamily: 'Playfair Display',
          border: 'solid black 1px', maxWidth: '100%',
          marginBottom: '2rem', marginTop: '2rem' }}>
        <video id='video' autoPlay playsInline></video>
        <input type='button' onClick={() => start()} value='Start Video' />
      </div>
    </div>
  );
}

export default App;
